}

.transparent {
    background: transparent !important;
}

.wrap {
    max-width: 960px;
    min-width: 360px;
    margin: 0 auto;
    position: relative;
}
.wrap.sticky {
    margin-top: 150px;
    /* set height from header */
}

.fluid .wrap {
    max-width: 100% !important;
}

